.homeDiv {
  height: auto;
  width: 100%;
  padding-top: 100px;
}

hr {
  border: 3px solid #545fc4;
  margin: 100px 100px 0px 100px;
  background-color: #545fc4;
}

.anchor {
  text-decoration: "none";
  color: "#545fc4";
}

.anchor:hover {
  text-decoration: "none";
  color: "white";
}

.container {
  display: flex;
}

.start {
  width: 100%;
  height: 90%;
  border-radius: 50%;
}

.leftContainer {
  height: 330px;
  width: 300px;
  margin-top: 130px;
  margin-left: 50px;
  /* border-radius: 15px; */
  display: flex;
  justify-content: center;
  align-items: center;
  /* border: 1px solid red; */
}

.rightContainer {
  width: 700px;
  font-size: 44px;
  margin: 100px 0px 0px 200px;
  color: rgb(232, 225, 225);
  font-family: "Roboto Slab", serif;
  line-height: 60px;
  /* border: 1px solid orange; */
}

.rightContainer h3 {
  font-size: 28px;
}

.typed {
  /* border: 1px solid blue; */
  margin-top: -35px;
  height: 150px;
  line-height: 80px;
}

.name {
  color: grey;
}

.role {
  color: violet;
  /* font-weight: bold; */
  font-family: "Courier New", Courier, monospace;
}

.resumeBtn {
  padding: 0.8em 1.8em;
  border: 3px solid #545fc4;
  position: relative;
  overflow: hidden;
  background-color: transparent;
  text-align: center;
  font-size: 16px;
  font-weight: bold;
  transition: 0.3s;
  z-index: 1;
  font-family: inherit;
  color: #545fc4;
  margin-top: 20px;
  cursor: pointer;
}

.resumeBtn::before {
  content: "";
  width: 0;
  height: 300%;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%) rotate(45deg);
  background: #545fc4;
  transition: 0.5s ease;
  display: block;
  z-index: -1;
}

.resumeBtn:hover {
  color: white;
}

.resumeBtn:hover::before {
  width: 105%;
}

@media (min-width: 481px) and (max-width: 768px) {
  .homeDiv {
    width: 100%;
    margin-bottom: 600px;
  }
  .container {
    display: grid;
    grid-template-columns: repeat(1, 100%);
  }
  .rightContainer {
    text-align: center;
    margin: auto;
    width: 100%;
    margin-top: 100px;
    font-size: 2.5rem;
    /* border: 1px solid red; */
  }
  .resumeBtn {
    height: 100px;
    width: 300px;
    font-size: 2rem;
    margin-top: 50px;
  }
  .leftContainer {
    margin: auto;
    margin-top: 150px;
    width: 100%;
    /* border: 1px solid yellow; */
  }
  .start {
    width: 350px;
    height: 350px;
    /* padding-bottom: 100px; */
  }
  hr {
    margin-top: 50px;
    width: 600px;
    margin-left: 85px;
  }
}

@media (min-width: 320px) and (max-width: 480px) {
  .homeDiv {
    width: 100%;
  }
  .container {
    display: grid;
    grid-template-columns: repeat(1, 100%);
  }
  .rightContainer {
    text-align: center;
    margin: auto;
    /* margin-top: 20px; */
    font-size: 2rem;
    width: 100%;
    line-height: 60px;
    /* border: 1px solid red; */
  }

  .typed {
    font-size: 1.2rem;
    line-height: 50px;
    margin-bottom: -40px;
  }

  .resumeBtn {
    height: 60px;
    width: 170px;
    font-size: 1rem;
  }
  .leftContainer {
    margin: auto;
    /* width: 200px; */
    /* border: 1px solid blue; */
  }
  .start {
    width: 150px;
    height: 150px;
    margin-top: -70px;
  }
  hr {
    margin-top: -70px;
    width: 290px;
    margin-left: 50px;
  }
}
